<template>
    <div id="product">
        <Master />
        <Loader v-show="isLoad"></Loader>
        <section class="container">
            <div class="row my-4">
                <div class="col-md-4 col-sm-6 mb-3" v-for="(p, i) in products" :key="i">
                    <div class="card">
                        <div class="card-header" style="height: 100px;">
                            <small>{{ p.category }}</small>
                            <p>{{ p.title }}</p>

                        </div>
                        <div class="card-body">
                            <div class="d-flex justify-content-center overflow-hidden" style="width:100%;height: 300px;">
                                <img :src="p.image" alt="">
                            </div>
                        </div>
                        <div class="card-footer">
                            <span>Price: <small>${{ p.price }}</small></span>
                            <a href="" class="btn btn-dark float-end">Add To Cart</a>
                        </div>
                    </div>
                </div>

            </div>
        </section>
    </div>
</template>

<script>
import Loader from './components/loader.vue'
// import NavBar from './Layouts/Nav.vue'
import Master from '@/views/Layouts/Master.vue'
import axios from 'axios'

export default {

    name: 'ProductList',
    components: { Loader, Master },
    data() {
        return {
            isLoad: true,
            products: [],
        };
    },
    created() {
        axios.get('https://fakestoreapi.com/products')
            .then((res) => {
                this.products = res.data;
                this.isLoad = false;
                // console.log(res.data.json());
            });
    },
}
</script>
